import { HttpClient, HttpParams, HttpHeaders } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { Section } from 'src/app/Models/section.model';
import { Topic } from 'src/app/Models/topic.model';
import { TopicPost } from 'src/app/Models/post.model';
import { PostLike } from 'src/app/Models/postlike.model'
let headers1: HttpHeaders = new HttpHeaders();
headers1 = headers1.append('Content-Type', 'application/json');
//headers1 = headers1.append('Authorization', 'application/json');

@Injectable({ providedIn: "root" })
export class DiscussionForumService 
{
  constructor(private httpClient: HttpClient) { }
  baseUrl = `${environment.serviceBaseUrl}`;
  sectionObj = new Section();
  objTopic =new Topic();
  objPost=new TopicPost();
  objPostLike = new PostLike();
  getAllSections() 
  {
    return this.httpClient.get(
        "http://ec2-13-233-60-5.ap-south-1.compute.amazonaws.com:7780/section/getAllSections"
    );
  }
  
  getAllTopics() 
  {
    return this.httpClient.get(
        "http://ec2-13-233-60-5.ap-south-1.compute.amazonaws.com:7780/topic/getAllTopics"
    );
  }

  createSectionDetails(sectionObj)
  {
    return this.httpClient.post(
      "http://ec2-13-233-60-5.ap-south-1.compute.amazonaws.com:7780/section/newSection",
      sectionObj,{ headers: headers1, responseType: 'text' }
    );
  }

  createTopicDetails(objTopic)
  {
    return this.httpClient.post(
      "http://ec2-13-233-60-5.ap-south-1.compute.amazonaws.com:7780/topic/newTopic",
      objTopic,{ headers: headers1, responseType: 'text' }
    );
  }

  createPostDetails(objPost)
  {
    return this.httpClient.post(
      "http://ec2-13-233-60-5.ap-south-1.compute.amazonaws.com:7780/topic/" + objPost.id,
      objPost,{ headers: headers1, responseType: 'text' }
    );
  }

  getSectionDashboardDetails()
  {
    return this.httpClient.get(
      "http://ec2-13-233-60-5.ap-south-1.compute.amazonaws.com:7780/section/getSectionData"
  );
  }

  getAllTopicsBysectionID(sectionID: any)
  {
    return this.httpClient.get(
      "http://ec2-13-233-60-5.ap-south-1.compute.amazonaws.com:7780/topic/getAllTopicsBySectionId/" + sectionID
  );
  }

  getAllPostDashboardDataByTopicID(topicID: any)
  {
    return this.httpClient.get(
      "http://ec2-13-233-60-5.ap-south-1.compute.amazonaws.com:7780/post/getAllPostsByTopicId/" + topicID
  );
  }

  postLikeAndDisLike(objPostLike)
  {
    return this.httpClient.post(
      "http://ec2-13-233-60-5.ap-south-1.compute.amazonaws.com:7780/post/saveLike",
      objPostLike,{ headers: headers1, responseType: 'text' }
    );
  }

  postRate(objPostLike)
  {
    return this.httpClient.post(
      "http://ec2-13-233-60-5.ap-south-1.compute.amazonaws.com:7780/post/saveRate",
      objPostLike,{ headers: headers1, responseType: 'text' }
    );
  }
}